            "text": "\nvar merge = function (nums1, m, nums2, n) {\n  // nums1#62index m+n-1 nums1\n  // \n  let current = m + n - 1;\n\n  while (current >= 0) {\n    // \n    if (n === 0) return;\n\n    // \n    if (m < 1) {\n      nums1[current--] = nums2[--n];\n      continue;\n    }\n\n    if (n < 1) {\n      nums1[current--] = nums1[--m];\n      continue;\n    }\n    //  nums1\n    //  m  n\n    if (nums1[m - 1] > nums2[n - 1]) {\n      nums1[current--] = nums1[--m];\n    } else {\n      nums1[current--] = nums2[--n];\n    }\n  }\n};\n"
