            "text": "\n/**\n * @param {number[]} g\n * @param {number[]} s\n * @return {number}\n */\nconst findContentChildren = function (g, s) {\n    g = g.sort((a, b) => a - b);\n    s = s.sort((a, b) => a - b);\n    let gi = 0; // \n    let sj = 0; // \n    let res = 0;\n    while (gi < g.length && sj < s.length) {\n        //  sj >=  gi \n        if (s[sj] >= g[gi]) {\n            gi++;\n            sj++;\n            res++;\n        } else {\n            //  sj <  gi \n            sj++;\n        }\n    }\n    return res;\n};\n"
